/******************************************************************

Stylesheet: TYPOGRAPHY

******************************************************************/

body {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
}
/******************************************************************
H1, H2, H3, H4, H5 STYLES
******************************************************************/

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5 {
	text-rendering: optimizelegibility;
	line-height:1.2;

	/* removing text decoration from all headline links */
	a {
		text-decoration: none;
	}
}

h1, .h1 {
	font-size: 3.2em;
	line-height: 1.1;
}

h2, .h2 {
	font-size: 2.5em;
	margin-bottom: 0.375em;
}

h3, .h3 {
	font-size: 2em;
	margin-bottom:0.5em;
}

h4, .h4 {
	font-size: 1.7em;
}

h5, .h5 {
	font-size: 1.5em;
	text-transform: uppercase;
	letter-spacing: 2px;
}

.page-title {
	text-align:center;
	font-weight:300;
	font-style:normal;
	line-height:1.2;
	margin:60px 0 30px;
}


/******************************************************************
.ENTRY-CONTENT STYLES
******************************************************************/

.entry-content {
	text-rendering: optimizelegibility;

	p{
		font-size:18px;
		line-height:1.5;
		max-width:720px;
		letter-spacing: -0.04em;
		margin:13px 0 3px;
	}

	hr {
		border: 0;
		width:50%;
		@include rounded(2px);
		height:1px;
		margin:2em auto 2.5em;
		background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(200, 200, 200, 0.7), rgba(0, 0, 0, 0));
	}

	hr.scissors {
	  border-top: 1px dashed #8c8b8b;
	  background-image:none;
	  height:18px;
	  margin:2em auto;
	}
	hr.scissors:after {
	  content: '\002702';
	  display: inline-block;
	  position: relative;
	  top: -12px;
	  left:50%;
	  margin-left:-10px;
	  padding: 0 3px;
	  background: #ffffff;
	  color: #8c8b8b;
	  font-size: 18px;
	}

	& > p:first-of-type, p.introParagraph{
		font-size:1.3em;
		line-height:1.2;

		&:first-child:first-letter {
			float: left;
			font-size: 3.8em;
			line-height:1;
			padding-top:.02em;
			padding-right:.2em;
			font-weight:700;
		}
	}

	ul, ol, table, dl {}

	ul, ol {

		padding:0 1em;

		li {margin:0 0 1.4em 1em;}

	}

	ul {
		list-style: disc;
	}

	ol {
		list-style: decimal;
	}

	blockquote {
		position:relative;
		margin:2em 0;
		padding:.35em 2em .35em 2em;
		font-family:$serif;
		background:#f7f7f7;
		-webkit-box-shadow: 0 5px 0 0 rgba(0,0,0,.3);
		box-shadow: 0 5px 0 0 rgba(0,0,0,.3);
		@include rounded(4px);

		&:before {
			margin-right:5px;
			position:absolute;
			top:10px;
			left:10px;
			color:#e7e7e7;
		    font-size: 24px;
		    text-rendering: auto;
		    -webkit-font-smoothing: antialiased;
		    -moz-osx-font-smoothing: grayscale;
		    content: "\f10d";
		}

		&:after {
			margin-right:5px;
			position:absolute;
			bottom:10px;
			right:10px;
			color:#e7e7e7;
		    font-size: 24px;
		    text-rendering: auto;
		    -webkit-font-smoothing: antialiased;
		    -moz-osx-font-smoothing: grayscale;
		    content: "\f10e";
		}

		p {
			margin:1.5em auto;
		}
	}
	dl {}

	dt {}

	dd {
		margin-left: 0;
		font-size: 0.9em;
		color: #787878;
		margin-bottom: 1.5em;
	}

	img {
		margin:0 0 36px;
		max-width: 100%;
		height: auto;
	}

	.alignnone {
	}

	.alignleft, img.alignleft {
		margin-right: 20px;
		margin-left:-20px;
		border-left:none !important;
		display: inline;
		float: left;
	}

	.alignright, img.alignright {
		display: inline;
		float: right;
	}

	.aligncenter, img.aligncenter {
		margin-right: auto;
		margin-left: auto;
		display: block;
		clear: both;
	}

	video, object {
		max-width: 100%;
		height: auto;
	}

	address {
		padding:20px;
	}

	pre {
		background: lighten($grey,30%);
		color:$grey;
		border: 2px solid $grey;
		@include rounded(2px);
		line-height:1.4;
		font-size:.9em;
		padding: 2em;
	}
}/*/entry-content*/

/******************************************************************
RESPONSIVE TYPOGRAPHY
******************************************************************/

@media only screen and (min-width: 481px) {

} // end of media query

@media only screen and (min-width: 768px) {

	.single-title, .page-title {
		font-size:60px;
		line-height:72px;

		span.titleSection a {
			float:left;
			height:60px;
			padding:0 20px;
			margin-right:12px;
			line-height:60px;
			font-size:30px;
		}
	}

	.article-header {
		.excerpt {

		}
	}

	.entry-content p {
		font-size:21px;
		line-height:1.58;
		margin-bottom:30px;
	}

	.entry-content > p:first-of-type, p.introParagraph{
		font-size:1.5em;
		line-height:1.3;
	}

} // end of media query

@media only screen and (min-width: 1030px) {

} // end of media query

@media only screen and (min-width: 1240px) {
	.single-title, .page-title {
		font-size:4em;
	}


} // end of media query
