/******************************************************************

Stylesheet: Base Mobile Stylesheet

******************************************************************/

/*********************
GENERAL STYLES
*********************/

body {
	background:$blue;
	font-size:17px;
}

::selection {
	background:$accent-color;
	color:#000;
}

::-moz-selection {
	background: $accent-color;
	color:#000;
}

a {cursor:pointer;}

.mobileHide, .show1240 {
	display:none;
}

.sr-only {
	position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0,0,0,0);
    border: 0;
}

/*********************
LAYOUT & GRID STYLES
*********************/
#container {
	position:relative;
}
.wrap {
	margin: 0 auto;
	width:768px;
	max-width:92%;
}

.wrap-960 {
	width:960px;
}

#content, .content{
	max-width:92%;
	padding:3% 4%;
	background:#fff;
	border:1px solid $light-grey;
	margin:30px auto;
	@include rounded(3px);
}

/*********************
LINK STYLES
*********************/

a, a:visited {
	color: $link-color;

	/* on hover */
	&:hover, &:focus {
		color: $link-hover;
	}

	/* on click */
	&:active {
		color:$accent-color;
	}

	/* mobile tap color */
	&:link {
		/*
		this highlights links on iPhones/iPads.
		so it basically works like the :hover selector
		for mobile devices.
		*/
		-webkit-tap-highlight-color : rgba( 0, 0, 0, 0.3 );
	}
}

/*********************
HEADER STYLES
*********************/
.home .header {
	background: #43a7da;
	background: -moz-linear-gradient(-20deg, darken($primary-color,1%) 0%, darken($primary-color,20%) 100%);
	background: -webkit-linear-gradient(-25deg, darken($primary-color,1%) 0%,darken($primary-color,20%) 100%);
	background: linear-gradient(160deg, darken($primary-color,1%) 0%,darken($primary-color,20%) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='darken($link-color,1%)', endColorstr='darken($link-color,20%)',GradientType=1 );
}

.header {
	padding:0;
	position:relative;

	img {
		max-width:100%;
		height:auto;
	}

	.logo.sixcol {
		img {
			max-width:480px;
		}
	}

	.sixcol {
		padding:6% 4%;
		text-align:center;
		color:rgba(0,0,0,.6);

		h2 {
			line-height:1.2;
			margin:0;
			font-weight:900;
		}
	}
}

/*********************
NAVIGATION STYLES
*********************/

/*
all navs have a .nav class applied via
the wp_menu function; this is so we can
easily write one group of styles for
the navs on the site so our css is cleaner
and more scalable.
*/

#mainNav {
	float:right;
	ul {
		list-style: none;
		margin:0;
		padding:2px 4px;

		li {
			display:inline-block;

			a {
				text-decoration:none;
				border-top:7px solid darken($primary-color,35%);
				color:darken($primary-color,35%);
				padding:7px 12px;
				font-weight:900;
				font-size:14px;

				@include transition(all .2s ease-in-out);

				&:hover, &:active {
					background:$accent-color;
				}
			}
		}
	}
}

#primaryNavContainer {
	position:relative;
	overflow:hidden;
	padding:10px 3%;
	text-align:center;
	background:$grey;

	.nav-toggle {
	  -webkit-tap-highlight-color: rgba(0,0,0,0);
	  -webkit-touch-callout: none;
	  -webkit-user-select: none;
	  -moz-user-select: none;
	  -ms-user-select: none;
	  -o-user-select: none;
	  user-select: none;
	  display:block;
	  text-decoration:none;
	  color:#fff;

	  &:before {
	  	display: inline-block;
	  	margin-right:5px;
	    font: normal normal normal 14px/1 FontAwesome;
	    font-size: inherit;
	    text-rendering: auto;
	    -webkit-font-smoothing: antialiased;
	    -moz-osx-font-smoothing: grayscale;
	    content: "\f0c9";
	  }

	  &.active:before {
	  	content: "\f00d";
	  }
	}

	.nav-collapse {
		border-bottom: 0;
		@include transition(all .2s ease-in-out);

		ul.top-nav{
			margin: 0;
			padding: 0;
			width: 100%;
			display: block;
			list-style: none;
		}

		li {
	  		margin:0 0 5px;
	  		border:1px solid lighten($grey,10%);
	  		@include rounded(2px);

			a {
				display: block;
				padding: 10px 12px;
				color:darken($text-color,5%);
				text-decoration:none;
				font-weight:bold;
				@include rounded(2px);

				&:before {
				}

				/*
				remember this is for mobile ONLY, so there's no need
				to even declare hover styles here, you can do it in
				the style.scss file where it's relevant. We want to
				keep this file as small as possible!
				*/
				&:hover, &:focus {
					text-decoration:underline;
					color:darken($text-color,9%);
					background:lighten($grey,5%);

					&:before {
						opacity:1;
					}
				}
			}

			&:first-child {}

			&:last-child {}

			/*
			so you really have to rethink your dropdowns for mobile.
			you don't want to have it expand too much because the
			screen is so small. How you manage your menu should
			depend on the project. Here's some great info on it:
			http://www.alistapart.com/articles/organizing-mobile/
			*/


			ul.sub-menu,
			ul.children {
				margin:0;

				li {
					border:none;

					a {
						opacity:.7;
						font-weight:400;

						&:before {
							display:none;
							padding-right:7px;
							font-family:FontAwesome;
						    text-rendering: auto;
						    -webkit-font-smoothing: antialiased;
						    -moz-osx-font-smoothing: grayscale;
							content: "";
						}

						&:hover,
						&:focus {opacity:1;}

						&:link {}

					}

					&:first-child {}

					&:last-child {}

				}

			}

		} /* end .menu li */

		/* highlight current page */
		li.current-menu-item,
		li.current_page_item,
		li.current-page-ancestor {

			a:before {
				display:inline-block;
				padding-right:7px;
				line-height:1;
				font-family:FontAwesome;
			    text-rendering: auto;
			    -webkit-font-smoothing: antialiased;
			    -moz-osx-font-smoothing: grayscale;
				content: "\f08d";
			}
		}

	} /* end .nav */



	.nav-collapse {
	  clip: rect(0 0 0 0);
	  max-height: 0;
	  position: absolute;
	  display: block;
	  overflow: hidden;
	  zoom: 1;
	}

	.nav-collapse.opened {
	  max-height: 9999px;
	}

}

#sideNav {
	display:none;
}


