/******************************************************************
PAGE NAVI STYLES
******************************************************************/

.page-navigation,
.wp-prev-next {
	margin: 1.1em 0 2.2em;
}
	.bones_page_navi {

		li {
			float: left;
			margin-right: 10px;

			a {
				font-family: $sans-serif;
				border:none;
				padding: 19px 30px 16px;
				color: $text-color;
				background:$light-grey;
				display: inline-block;
				font-size: 18px;
				line-height:1;
				font-weight: bold;
				text-transform:uppercase;
				text-decoration: none;
				cursor: pointer;
				-webkit-box-shadow: 0 5px 0 0 $grey;
				box-shadow: 0 5px 0 0 $grey;
				margin-top:10px;
				margin-bottom: 20px;

				@include rounded(3px);

				i {margin-right:3px;}

				&:hover, &:focus {
					color: #333;
					background:#f1f1f1;
				}

				&:active {
					background:darken($grey,5%);
					color:lighten($grey,5%);
					box-shadow:none;
					margin-top:15px;
					margin-bottom:15px;
				}

			}

		}

		/* current page link */
		li.bpn-current {

			padding: 24px 15px 15px;
			color: $text-color;
			display: inline-block;
			font-size: 18px;
			font-weight:bold;


			a {
				/* hovering on current page link */
				&:hover,
				&:focus {}

			}

		} /* end .bones_page_navi .bpn-current */

		/* common styles for page-navi links */
		li.bpn-prev-link,
		li.bpn-next-link {

			a {

			}
		}

		/* remove the bg on end links */
		li.bpn-prev-link a:hover,
		li.bpn-next-link a:hover,
		li.bpn-first-page-link a:hover,
		li.bpn-last-page-link a:hover {
		}

		/* previous link */
		li.bpn-prev-link {
			a {
				&:hover,
				&:focus {}
			}
		}

		/* next page link */
		li.bpn-next-link {
			a {
				&:hover,
				&:focus {}
			}
		}

		/* first page link */
		li.bpn-first-page-link {
			a {
				&:hover,
				&:focus {}
			}
		}

		/* last page link */
		li.bpn-last-page-link {
			a {
				&:hover,
				&:focus {}
			}
		}

	} /* end .bones_page_navi */

	/* fallback previous & next links */
	.wp-prev-next {

		ul {}

		.prev-link {
			float: left;
		}

		.next-link {
			float: right;
		}

	} /* end .wp-prev-next */

