/*********************
SEARCH STYLES
*********************/

.searchContainer {
	@include rounded(3px);
	position:relative;

	&:before {
		content: "\f002";
		display: inline-block;
		position:absolute;
		left:12px;
		top:12px;
		z-index:50;
		color:$grey;
		font-size:3em;
		opacity:.8;
		font-family: FontAwesome;
		font-style: normal;
		font-weight: normal;
		line-height: 1;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}


	.screen-reader-text {
		visibility: hidden;
		text-indent:-9999px;
		width:0px;
		height:0px;
		display:block;
		overflow:hidden;
	}

	.searchsubmit {
		display:none;
		visibility: hidden;
		text-indent:-9999px;
		width:0px;
		height:0px;
		overflow:hidden;
	}

	input[type=text]{
		height:70px;
		width:100%;
		padding-left:70px;
		line-height:75px;
		position:relative;
		margin:0 0 5px;
		font-style:regular;
		font-weight:800 !important;
		font-size:18px;
		color:rgba(200,200,200,.9);
		border:none;
		background:lighten($light-grey,4%);
		-webkit-box-shadow: 0 5px 0 0 darken($grey,8%);
		box-shadow: 0 5px 0 0 darken($grey,8%);
		@include transition(padding .2s ease-in-out);


		@include rounded(3px);

		&:focus {
			background:lighten($light-grey,6%);
			color:$text-color;
			outline: none;
			box-shadow:none;
			margin:5px 0 0;
			z-index:55;
			padding-left:25px;
			-webkit-box-shadow:inset 0 5px 10px 0 rgba(0,0,0,.1);
			box-shadow:inset 0 5px 10px 0 rgba(0,0,0,.1);
		}

		::-webkit-input-placeholder { /* WebKit browsers */
	    color:rgba(200,200,200,.9);
		}
		:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
		   color:rgba(200,200,200,.9);
		   opacity:  1;
		}
		::-moz-placeholder { /* Mozilla Firefox 19+ */
		   color:rgba(200,200,200,.9);
		   opacity:  1;
		}
		:-ms-input-placeholder { /* Internet Explorer 10+ */
		   color:rgba(200,200,200,.9);
		}

	}

}

