/*********************
TESTIMONIAL STYLES
*********************/

.testimonialWrap {
	margin:.5em 0;
	position:relative;

	a {color:lighten($text-color,15%)}

	.paging {
		display:none;
	}

	blockquote {
		margin:0;
		padding:0;
	}

	.testimonial {
		position:relative;
		font-family:$theme-font;
		font-weight:300;
		font-variant:italic;
		font-style:italic;
		text-align:center;
		font-size:1.4em;
		padding:0 1.2em;
		line-height:1.4;
		color:lighten($text-color,35%);

		&:before, &:after {
			display: inline-block;
			position:absolute;
			font-family: FontAwesome;
			font-style: normal;
			font-weight: normal;
			opacity:.5;
			line-height: 1;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
		}

		&:before {
			content: "\f10d";
			top:2px;
			left:5px;
		}

		&:after {
			content: "\f10e";
			bottom:0px;
			right:5px;
		}

		& > p:first-child {
			margin-top:0;
		}

	}

	.author, .url {
		margin:5px 15%;
		text-align:right;
		line-height:1;
		font-size:.8em;
	}
}

