.footer {
	padding:0;
	margin-top:4%;
	background-image:url(../images/tile-graph-teal.png);
	background-repeat:repeat;
	background-color:darken($teal,15%);
	background-attachment:fixed;

	#inner-footer {
		background:rgba(6,35,38,.5);
		padding:4%;
		-webkit-box-shadow:inset 0 20px 80px -20px rgba(0,0,0,.5);
		box-shadow:inset 0 20px 80px -20px rgba(0,0,0,.5);

		h3 {
			color:lighten($link-color,5%);
		}

		a {
			color:lighten($link-color,5%);
			text-decoration:none;

