				margin-right:6px;
			}

			&:hover {
				color:lighten($link-color,10%);
				text-decoration:underline;
			}
		}

		.sixcol {
			float:left;
			width: 47.5%;
			margin-left:5%;

			&.first {margin-left:0%}
		}

		.footerLogo {
			text-align:center;
			img {
				opacity:.75;
				@include transition(all .2s ease-in-out);

				&:hover {opacity:1;}
			}

			&#footerUncopyright img{
				max-width:200px;
				opacity:.6;

				&:hover {opacity:.8;}
			}
		}

		#footerVid {
			.loopCard {
				background:none;

				.loopImage:before {
					font-size:50px;
					width:50px;
					height:50px;
					margin:-25px 0 0 -25px;
					opacity:.8;
					z-index:100;
				}

				.loopTitle {
					margin:5px auto;

					&:before {display:none;}
				}

				.featuredImage {
					margin:0;
				}

				&:hover .loopImage:before {opacity:1}

				.loopImage img {opacity:.8}
			}
		}

		#footerCredits {
			p {margin:8px auto;line-height:1.2}
		}
	}
}


