	.gallery-columns-2 > .gallery-item:nth-of-type(2n + 1), .gallery-columns-3 > .gallery-item:nth-of-type(3n + 1), .gallery-columns-4 > .gallery-item:nth-of-type(4n + 1), .gallery-columns-5 > .gallery-item:nth-of-type(5n + 1) {
